* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #fafafa;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen-Sans, Ubuntu, Cantarell, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

img {
  max-width: 100%;
  display: block;
}

#datetime-picker {
  width: 250px;
  padding: 5px;
  font-size: 20px;
  font-weight: 500;
}

button[data-start], button[data-stop] {
  padding: 5px;
  font-size: 20px;
  font-weight: 500;
}

.timer {
  margin-top: 20px;
  display: flex;
}

.field + .field {
  margin-left: 15px;
}

.field > span {
  text-align: center;
  font-weight: 500;
  display: block;
}

.value {
  font-size: 40px;
}

.label {
  text-transform: uppercase;
}
